.nav {
  display: grid;
  grid-column-gap: 1rem;
  grid-template-columns: 1fr 1fr;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

@media (min-width: 480px) {
  .nav {
    grid-row-gap: 1rem;
    grid-template-columns: 1fr;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todo-item label {
  margin-bottom: 0;
}

.todo-item__btn-close {
  align-self: start;
}

.add-todo {
  display: flex;
  flex-direction: column;
}

.add-todo .nes-input {
  border-image-outset: 1;
  margin: 0;
  margin-bottom: 1rem;
}

@media (min-width: 480px) {
  .add-todo {
    display: grid;
    grid-column-gap: 0.5rem;
    grid-template-columns: 1fr auto;
  }

  .add-todo .nes-input {
    margin-bottom: 0;
  }
}

@font-face {
  font-family: 'Press Start 2P';
  font-style: normal;
  font-weight: 400;
  src: local('Press Start 2P Regular'), local('PressStart2P-Regular'),
    url(/hydrogen/static/media/press-start-2p-v7-latin-ext_latin-regular.0d24c903.woff2)
      format('woff2'),
    url(/hydrogen/static/media/press-start-2p-v7-latin-ext_latin-regular.074e10df.woff) format('woff');
}

.main {
  min-height: 100vh;
  padding-left: 2vh;
  padding-right: 2vh;
}

.container {
  display: grid;
  grid-template-columns: [sidebar] [content];
}

.sidebar {
  grid-column: sidebar;
}

.content {
  grid-column: content;
}

@media (min-width: 480px) {
  .container {
    grid-column-gap: 1rem;
    grid-template-columns: [sidebar] auto [content] 1fr;
  }
}

